import React from "react";
import { EditorScene } from "./components/Editor";
import { Provider } from "react-redux";
import { appStore } from "./immerReducers/appStore";
import { useParams } from "react-router-dom";
import { renderThreejs, unmountThreejs } from "./threejs/renderThreejs";
import { clearBoard, prepareBoard } from "./prepareBoard";
import { AppData } from "./AppData";
import { startBatchQueue, stopBatchQueue } from "./ApiService";

export const PageBoard: React.FC<{
  data: AppData;
}> = (props) => {
  // React.useEffect(() => {
  // appStore.dispatch({
  //   type: "startAppEvent",
  //   payload: { state: appStore.getState() },
  // });
  // appStore.dispatch(Actions.Persist.action_readSavedCanvas());
  // }, []);
  let { uuid } = useParams<{ uuid: string }>();

  React.useEffect(() => {
    renderThreejs(document.getElementById("threejs"));
    prepareBoard(uuid);

    if (uuid !== "demo" && props.data.save_events === true) {
      startBatchQueue(uuid);
    }

    return () => {
      stopBatchQueue(uuid);
      clearBoard(uuid);
      unmountThreejs(document.getElementById("threejs"));
    };
  }, []);

  return (
    <div className="App">
      <Provider store={appStore}>
        <EditorScene />
      </Provider>
    </div>
  );
};
